  display: none;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Monserrat", sans-serif;
  font-size: 1.6rem;
  margin: 0;
}
body.fixed-body {
  overflow: hidden;
}
body.scroll-body {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0, 0, 0, 0;
}

h1 {
  font-size: 4rem;
  margin-top: 0;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.header {
  background-color: #4CB8B3;
}
@media (min-width: 768px) {
  .header.fixeded {
    left: 0;
    top: 0;
    position: fixed;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
    width: 100%;
  }
}
.header h1 {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .container-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .container-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .main-nav {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
}
.main-nav a {
  color: #FFFFFF;
  font-size: 2rem;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .main-nav a {
    margin: 0;
  }
}
.main-nav :hover {
  color: #FEF46D;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 40rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(165, 3, 255);
  background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.container-video {
  z-index: 2;
}
.container-video h2 {
  color: #FFFFFF;
  font-size: 7rem;
  margin: 0;
}
.container-video p {
  background-color: #F53756;
  display: inline-block;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
  margin: 0;
}

.about-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .about-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.festival-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 0;
}
@media (min-width: 768px) {
  .festival-content {
    margin-top: 0;
  }
}
.festival-content h2 {
  margin-bottom: 2rem;
}
.festival-content .date {
  color: #F53756;
  font-weight: 700;
}

.lineup {
  background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  padding: 5rem;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #FFFFFF;
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .container-card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.container-card p {
  color: #000000;
}

.escenary {
  margin-top: 5rem;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .escenary {
    margin-top: 0;
  }
  .escenary .escenary-name {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.escenary .escenary-name:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.EDM .escenary-name:after {
  background-image: url("../img/icono_edm.png");
}

.Rock .escenary-name:after {
  background-image: url("../img/icono_rock.png");
}

.bg-yellow {
  background-color: #FEF46D;
}
.bg-yellow li:nth-child(odd) {
  background-color: #fdee21;
}

.bg-green {
  background-color: #4CB8B3;
}
.bg-green li:nth-child(odd) {
  background-color: #3b9691;
}

.calendary {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendary li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendary span {
  font-weight: 400;
}

.galery {
  padding: 5rem 0;
}
.galery h3 {
  text-align: center;
  margin-bottom: 5rem;
}

.images-galery {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .images-galery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .images-galery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .images-galery {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-close {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-close:hover {
  cursor: pointer;
  color: red;
}

.tickets {
  padding: 5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .container-tickets {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pass .header-pass {
  background-color: #F53756;
  padding: 0.5rem;
}
.pass .header-pass p {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pass .body-pass {
  background-color: #FFFFFF;
}
.pass .body-pass ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pass .body-pass ul li, .pass .body-pass ul :nth-child(odd) {
  background-color: #FFFFFF;
  padding: 1rem;
  border-bottom: 1px solid #E1E1E1;
}
.pass .footer-pass {
  background-color: #752f97;
  padding: 0.5rem;
}
.pass .footer-pass p {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  font-size: 2.4rem;
  font-weight: 700;
}

.name-pass {
  text-align: center;
  color: #FFFFFF;
  font-size: 2.4rem;
}

.site-footer {
  background-color: #F53756;
  padding: 1rem;
}
.site-footer p {
  text-align: center;
  margin: 0;
  color: #FFFFFF;
}