@use 'variables' as v;

html{
  font-size: 62.5%;
  box-sizing: border-box;
}

*,*::before,*::after{
  box-sizing: inherit;
}

body{
  font-family: v.$Main-Font;
  font-size: 1.6rem;
  margin: 0;

  &.fixed-body{
    overflow: hidden;
  }

  &.scroll-body{
    padding-top: 8.6rem;
  }
}

p{
  font-size: 2rem;
  color: v.$Black;
  line-height: 1.5;
}

img{
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.container{
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a{
  text-decoration: none;
}

h1,h2,h3{
  margin: 0, 0, 0, 0;
}

h1{
  font-size: 4rem;
  margin-top: 0;
}

h2{
  font-size: 3.6rem;
}

h3{
  font-size: 3rem;
}